body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #f4f4f4;
}

h1 {
  background-color: #1c5a8d;
  color: white;
  padding: 10px;
}

.container {
  margin: 20px auto;
  max-width: 400px;
}

input[type="text"] {
  width: 70%;
  padding: 10px;
  margin-right: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 15px;
  background-color: #1c5a8d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #023d77;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  background: white;
  margin: 5px 0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

label {
  display: block;
}

/*Chatbot Styles*/
#chatbot-container {
  width: 300px;
  border: 1px solid #ccc;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: white;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

#chat-history {
  height: 200px;
  overflow-y: auto;
  border-bottom: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 5px;
}

#chat-input {
  width: calc(100% - 60px);
  padding: 5px;
  margin-right: 5px;
}

#send-btn {
  padding: 5px 10px;
  cursor: pointer;
}

.history {
  padding-bottom: 15px;
}
